.Wedding-header {
  background-color: #1F2E56;
  padding: 20px;
  color: #E0DAE0; }

.Wedding-header-link {
  font-family: 'Galada', cursive, sans-serif;
  font-size: 1.2em;
  font-weight: bold;
  color: #E0DAE0;
  opacity: .8;
  text-decoration: none;
  margin-right: 1em; }

.Wedding-header-link.is-active {
  opacity: 1;
  color: #FAC2F4; }

@media (max-width: 45rem) {
  .Wedding-header {
    text-align: center; } }

.Wedding-hero {
  margin: 0 0 2em 0;
  background-size: 100% 100%;
  position: relative; }
  .Wedding-hero img {
    max-width: 100%;
    height: auto;
    opacity: .65; }

.Wedding-text-overlay {
  position: absolute;
  top: 15%;
  left: 30%;
  text-shadow: -3px -1px 0 #1F2E56, 1px -1px 0 #1F2E56, -3px 1px 0 #1F2E56, 1px 1px 0 #1F2E56; }

.Wedding-text-overlay .small {
  padding-left: 30%; }

.Wedding-overlay {
  color: #FAC2F4;
  font-weight: bold;
  font-family: 'Stalemate', cursive, sans-serif; }

.large {
  font-size: 8rem; }

.small {
  font-size: 3.2rem; }

@media (max-width: 45rem) {
  .Wedding-text-overlay {
    text-shadow: -1px -1px 0 #1F2E56, 1px -1px 0 #1F2E56, -1px 1px 0 #1F2E56, 1px 1px 0 #1F2E56; }
  .small {
    font-size: 1em; }
  .large {
    font-size: 2em; } }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #1F2E56;
  height: 150px;
  padding: 20px;
  color: #E0DAE0; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large;
  color: #1F2E56; }

.wedding-container {
  max-width: 80%;
  margin: auto; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



